import React from 'react';

import BoardRowContainer from './../../containers/BoardRowContainer';
import BoardListHeaderContainer from './../../containers/BoardListHeaderContainer';
import BoardListFooter from './BoardListFooter';
import Spinner from './../Spinner';

export default class BoardList extends React.Component {

	static propTypes = {
		boardList: React.PropTypes.object,
		categories: React.PropTypes.array
	};

	constructor(props) {
		super(props);

		this.onChangeCheckAll = this.onChangeCheckAll.bind(this);
	}

	componentWillMount() {
		this.props.fetchBoardIndex();
	}

	onChangeCheckAll(e) {
		let target = e.target.checked;

		if(target) {
			this.props.handleCheckAll();
		} else {
			this.props.handleUnCheckAll();
		}
	}

	render() {

		if(this.props.loading) {
			return <Spinner />
		}

		return (
			<div>
				<BoardListHeaderContainer />

				<div className="board_list">
					<table>
						<thead className="xe-hidden-xs">
						<tr>
							{
								(() => {
									if(Common.get('user').isManager) {
										return (
											<th scope="col">
												<label className="xe-label">
													<input type="checkbox" onChange={ this.onChangeCheckAll } />
													<span className="xe-input-helper"></span>
													<span className="xe-label-text xe-sr-only"> </span>
												</label>
											</th>
										);
									}
								})()
							}
							<th scope="col" className="favorite"><span><a href="#" title="  "><i className="xi-star-o"></i><span className="xe-sr-only">  </span></a></span></th>
							{
								(() => {
									if(this.props.categories.length > 0) {
										return (<th scope="col"><span></span></th>);
									}
								})()
							}
							<th scope="col" className="title"><span></span></th>
							<th scope="col"><span></span></th>
							<th scope="col"><span><a href="#"></a></span></th>
							<th scope="col"><span><a href="#"></a></span></th>
						</tr>
						</thead>
						<tbody>
						{
							this.props.boardList.map((row, i) => {
								return (
										<BoardRowContainer {...row} />
									)
							})
						}
						</tbody>
					</table>
				</div>

				<BoardListFooter />
			</div>
		);
	}
};



